const galleryContainer = document.querySelector(".gallery");

function createGallery(images) {
  const imagesList = images
    .map((image) => {
      return `<li class="gallery-item">
   <a class="gallery-link" href="${image.original}">
     <img
       class="gallery-image"
       src="${image.preview}"
       width="360px"
       data-source="${image.original}"
       alt="${image.description}"
     />
   </a>
 </li>`;
    })
    .join("\n\n");
  galleryContainer.innerHTML = imagesList;
}
createGallery(images);

galleryContainer.addEventListener("click", (event) => {
  event.preventDefault();
  if (event.target === event.currentTarget) return;
  const liImg = event.target;
  const dataSource = liImg.dataset.source;
  const selectedImage = images.find((el) => el.original === dataSource);
  openGalleryModal(selectedImage);
});

function openGalleryModal(image) {
  const modal = basicLightbox.create(
    `<img
      class="gallery-image"
      src="${image.original}"
      alt="${image.description}"
    />`,
    {
      onShow: (instance) => {
        document.addEventListener("keydown", onModalClose);
      },
      onClose: (instance) => {
        document.removeEventListener("keydown", onModalClose);
      },
    }
  );

  modal.show();

  function onModalClose(event) {
    console.log(event.code);
    if (event.code === "Escape") {
      modal.close();
    }
  }
}
